/**
 * Command Groups and Navigation System
 * Implements intelligent grouping and navigation for CLI commands
 */

export interface CommandInfo {
  command: string;
  description: string;
  usage?: string;
  examples?: string[];
  category: CommandCategory;
  relatedCommands?: string[];
}

export type CommandCategory = 
  | 'user'
  | 'config' 
  | 'project'
  | 'dev'
  | 'agent'
  | 'chat'
  | 'media'
  | 'ui';

