// Get commands by category
export function getCommandsByCategory(category: CommandCategory): CommandInfo[] {
  return commands.filter(cmd => cmd.category === category);
}

// Get command info
export function getCommandInfo(command: string): CommandInfo | undefined {
  return commands.find(cmd => cmd.command === command);
}

// Get related commands
export function getRelatedCommands(command: string): CommandInfo[] {
  const cmd = getCommandInfo(command);
  if (!cmd || !cmd.relatedCommands) return [];
  
  return cmd.relatedCommands
    .map(relCmd => getCommandInfo(relCmd))
    .filter((info): info is CommandInfo => info !== undefined);
}

// Get command suggestions based on context
export function getContextualSuggestions(
  lastCommand?: string,
  projectInitialized?: boolean,
  userLoggedIn?: boolean
): CommandInfo[] {
  const suggestions: CommandInfo[] = [];
  
  // If user not logged in, suggest login
  if (!userLoggedIn) {
    const loginCmd = getCommandInfo('/login');
    if (loginCmd) suggestions.push(loginCmd);
  }
  
  // If project not initialized, suggest init
  if (!projectInitialized) {
    const initCmd = getCommandInfo('/init');
    if (initCmd) suggestions.push(initCmd);
  }
  
  // Add related commands from last command
  if (lastCommand) {
    const related = getRelatedCommands(lastCommand);
    suggestions.push(...related);
  }
  
  // Remove duplicates
  const uniqueSuggestions = suggestions.filter(
    (cmd, index, self) => self.findIndex(c => c.command === cmd.command) === index
  );
  
  return uniqueSuggestions.slice(0, 3); // Return top 3 suggestions
}

// Find command chain for a command
export function getCommandChain(command: string): typeof commandChains[keyof typeof commandChains] | undefined {
  return Object.values(commandChains).find(chain => 
    chain.commands.includes(command)
  );
}